<template>
<div class="fx-upload1">
    <!-- <header-text :headerText="headerTitle" @header-event="onHeaderBtn" class="fx-header"></header-text> -->
    <div class="fx-content">
        <div class="text">尊敬的 <b>{{holderName}}</b>：<br/>感谢您的信任并投保我公司。<br/>您为被保人（证件号码为{{insurederCardNo}}）递交的投保单号码为（{{policyNo}}）的投保申请，我公司已经收悉。<br/>现因业务处理及审核工作中出现了如下情况需要与您进一步沟通。</div>
        <div class="subTitle">原因：{{msg}}</div>
        <div class="subTitle" v-if="notification">请您认真阅读：</div>
        <a class="link_item" v-if="notification" :href="notification">新契约问题件通知函，点击查看详情</a>
        <div class="text">本内容涉及到投保单资料的确认，属于保险合同的组成部分。为使您的利益得到有效的保证，请您在收到本通知书后的{{period}}天内给与答复，并务必请您及 <b>{{insurederName}}</b> 或其监护人在本通知书上亲笔签字。</div>
        <div class="Title">客户反馈栏</div>
        <div class="text">我申明上述情况完整而真实，我知道本表格将是保险合同的一部分。</div>
        <div class="subTitle">1.影像资料</div>
    </div>
    <!-- 这里动态添加上传影像类型 -->
    <div class="upload_List">
       <div v-for="(value,index) in typeList" :key="index">
            <div v-if="value.chosed">
                <div class="typeTitle">{{value.name}}</div>
                <up-load @return-img="getImg" :upload_type="value.type"></up-load>
            </div>
            <div v-else>
                
            </div>
        </div> 
    </div>
    <div class="choseBtn" @click="showModal()">选择影像类型</div>
    <div class="fx-btns-space"></div>
    <div class="fx-btns">
        <div class="submitBtn" @click="subAddData">确认提交</div>
    </div>
    <div class="fx-modal" :style="{height:clientHeight1+'px'}">
        <div class="modal">
            <div class="header">选择影像类型</div>
            <div class="content">
                <div v-for="(value,index) in modalJson" :key="index" class="typeList">
                    <div v-if="value.chosed">
                        <div class="left">{{value.name}}</div>
                        <div class="right_chosed right" @click="unChose(index)"></div>
                    </div>
                    <div v-else>
                        <div class="left">{{value.name}}</div>
                        <div class="right_unchosed right" @click="Chose(index)"></div>
                    </div>
                </div>
            </div>
            <div class="footer">
                <div class="sure" @click="makeSure()">确定</div>
            </div>
        </div>
    </div>
</div>
</template>
    
<script>
import Vue from "vue";
import headerText from "@/components/header";
import popModal from "@/components/modal";
import upLoad from "@/components/upload";
import { Indicator } from 'mint-ui';
export default {
    name:'upload3',
    data(){
        return{
            orderId:null,
            insurederCardNo:'XXX',
            policyNo:'XXX',
            insurederName:'XXX',
            holderName:'XXX',
            period:'XXX',
            msg:'XXX',
            notification:null,
            clientHeight1:0,
            headerTitle:'资料上传',
            typeList:[],
            returnImg:{type1:[],type2:[],type3:[],type4:[],type5:[],type6:[]},
            modalJson:[
                {type:"type1",name:"病历资料",chosed:false,id:1},
                {type:"type2",name:"出院小结",chosed:false,id:2},
                {type:"type3",name:"体检报告",chosed:false,id:3},
                {type:"type4",name:"其他资料",chosed:false,id:4},
                {type:"type5",name:"各类问卷",chosed:false,id:5},
                {type:"type6",name:"收入证明",chosed:false,id:6}
            ],
        }
    },
    created() {

    },
    methods: {
        //获取图片列表
        getImg(imgList,type){
            this.returnImg[type] = imgList;
            console.log(this.returnImg);
        },
        //点击header
        onHeaderBtn(headerText){
            console.log(headerText);
        },
        //showModal
        showModal(){
            document.getElementsByClassName("fx-modal")[0].style.display="block";
        },
        //hideModal
        hideModal(){
            document.getElementsByClassName("fx-modal")[0].style.display="none";
        },
        //进入智能审核
        makeSure(){
            //console.log(this.modalJson);
            this.typeList = this.modalJson;
            this.hideModal();
        },
        //取消选择
        unChose(index){
            this.modalJson[index].chosed = false;
        },
        //选择
        Chose(index){
            this.modalJson[index].chosed = true;
        },
        //提交
        subAddData(){
            //fxPostAddData
            console.log(this.returnImg);
            let returnImg = this.returnImg;
            //有未上传的影像不允许提交
            console.log("this.modalJson",this.modalJson);
            console.log("this.typeList",this.typeList);
            let flag = false;
            this.typeList.map((item)=>{
                if(item.chosed && returnImg[item.type].length === 0)
                flag = true;
            })
            if(flag){
                this.$popup.msg("请上传相关资料！");
                return
            }
            let ImgList = returnImg['type1'].concat(returnImg['type2']).concat(returnImg['type3']).concat(returnImg['type4']).concat(returnImg['type5']).concat(returnImg['type6']);
            console.log("ImgList",ImgList);
            //没有上传影像也不允许上传
            if(ImgList.length === 0){
                this.$popup.msg("请上传相关资料！")
                return
            }
            let params={};
            ImgList.map((item,index)=>{
                params['upload_'+index] = item;
            })
            let THAT = this;
            Indicator.open({
                text: '加载中...',
                spinnerType: 'fading-circle'
            });
            THAT.$http.getHttp({
                name:'fxPostAddData',
                params:params,
                orderId:THAT.orderId,
                type:'post'
            },
            res => {
                Indicator.close();
                console.log("res",res);
                if(res.httpCode === 200){
                    THAT.$router.push({path:'/fuxing/waiting',query:{"orderId":THAT.orderId}});
                }
            })
        },
        //处理返回参数
        dealArr(arr,keyword){
            var value;
            arr.map((item)=>{
                if(item.split("=")[0] === keyword){
                    value = item.split("=")[1];
                }
            })
            return value;
        }
    },
    computed:{},
    components:{
        "header-text":headerText,
        "up-load":upLoad
    },
    mounted(){
        let orderId = this.$route.query.orderId;
        this.orderId = orderId;
        this.clientHeight1 = window.innerHeight;
        //fxGetAddData
        let THAT = this;
        Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
        });
        THAT.$http.getHttp({
            name:'fxGetAddData',
            params:{},
            orderId:THAT.orderId,
            type:'get'
        },
        res => {
            Indicator.close();
            console.log("res",res.data);
            //let arr = res.data.split("?")[1].split("&");
            let data = res.data;
            let insurederCardNo = data.insurederCardNo,policyNo = data.policyNo,
            insurederName = data.insurederName,msg = data.msg,
            notification = data.notification,period = data.period,holderName = data.holderName;
            THAT.insurederCardNo = insurederCardNo;
            THAT.policyNo = policyNo;
            THAT.insurederName = insurederName;
            THAT.notification = notification;
            THAT.period = period;
            THAT.msg = msg;
            THAT.holderName = holderName;
        })
    }
}
</script>

<style lang="scss">
.fx-upload1{
    position: relative;
    height: 100%;
    width:r(750);
    .upload_List{
        .typeTitle{
            font-size: r(30);
            height: r(90);
            line-height: r(90);
            padding-left: r(15);
        }
    }
    .fx-modal{
        width: 100%;
        position: fixed;
        bottom:0;
        background: rgba(0,0,0,0.3);
        display: none;
        z-index:99;
        .modal{
            width:r(580);
            height:r(825);
            border-radius: r(20);
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            top: 0;
            margin: auto;
            background: #fff;
            .header{
                height: r(112);
                font-size: r(34);
                color:#fff;
                font-weight: bold;
                line-height: r(112);
                background:url("../../../static/images/modal_header.png") no-repeat;
                background-size: r(580) r(112);
                text-indent:r(30);
            }
            .content{
                padding:0 r(30);
                font-size: r(28);
                margin-top:0;
                .typeList{
                    height: r(89);
                    width: 100%;
                    border-bottom: 1px solid #ddd;
                    &>div{
                        overflow: hidden;
                        height: r(88);
                        line-height: r(88);
                        .left{
                            width: r(478);
                            float: left;
                        }
                        .right{
                            width: r(42);
                            height:r(88);
                            float: left;
                        }
                        .right_chosed{
                            background:url("../../../static/images/chosed.png") center no-repeat;
                            background-size: r(42) r(42);
                        }
                        .right_unchosed{
                            background:url("../../../static/images/unchosed.png") center no-repeat;
                            background-size: r(42) r(42);
                        }
                    }
                }
            }
            .footer{
                padding:0 r(30);
                margin-top: r(50);
                .sure{
                    width: r(520);
                    height: r(88);
                    border-radius: r(50);
                    background: #0084ff;
                    color: #fff;
                    text-align: center;
                    line-height: r(88);
                    font-size: r(32);        
                }
            }
        }
    }
    .fx-content{
        padding-top:r(20);
        .text{
            font-size: r(30);
            line-height: r(44);
            color: #333;
            font-weight:lighter;
            margin: 0 r(15);
            white-space: pre-wrap;
        }
        .subTitle{
            line-height:r(44);
            padding-left: r(15);
            font-size: r(32);
            margin:r(21) 0;
            font-weight:normal;
            color: #000;
        }
        .link_item{
            color: #0084ff;
            text-align: center;
            margin-top: r(18);
            margin-bottom: r(40);
            font-size: r(30);
            display:block;
        }
        .Title{
            font-weight: bold;
            text-align: center;
            font-size: r(30);
            color: #000;
            margin-top: r(68);
            margin-bottom: r(26);
        }
    }
    .choseBtn{
        width:100%;
        height:r(46);
        line-height:r(40);
        color: #0084ff;
        font-size: r(36);
        font-weight:bold;
        text-align: center;
        border-bottom:r(2) solid #0084ff;
        width:r(280);
        margin:0 auto;
        margin-top:r(60);
        padding-bottom:r(5);
        margin-bottom:r(100);
    }
    .fx-btns-space{
        width:r(750);
        height:r(80);
    }
    .fx-btns{
        width:r(750);
        height:r(80);
        line-height:r(80);
        position:fixed;
        bottom:0;
        .submitBtn{
            width:100%;
            height:100%;
            background: #0084ff;
            color:#fff;
            font-size:r(34);
            text-align:center;
        }
    }
}
</style>


