<template>
    <div class="fx-modal" :style="{height:clientHeight1+'px'}">
        <div class="modal" :style="{height:modalHeight+'px'}">
            <div class="header">{{modalTitle}}</div>
            <div class="content">{{modalContent}}</div>
            <div class="footer">
                <div class="cancel" @click="Cancel()">取消</div>
                <div class="sure" @click="Sure()">确认</div>
            </div>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
export default {
    props: {
      modalTitle: String,
      modalContent:String,
      modalHeight:Number
    },
    data(){
        return{
            clientHeight1:0,
        }
    },
    created() {

    },
    methods: {
        Cancel(){
            this.$emit('cancel-event');
        },
        Sure(){
            this.$emit('sure-event');
        }
    },
    computed:{},
    mounted(){
        this.clientHeight1 = window.innerHeight;
    },
    watch:{
        headerText(newVal){
            console.log(newVal);
        }
    }
};
</script>

<style lang="scss">
.fx-modal{
    width: 100%;
    position: absolute;
    bottom:0;
    background: rgba(0,0,0,0.3);
    display: none;
    .modal{
        width:r(580);
        height:r(473);
        border-radius: r(20);
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        margin: auto;
        background: #fff;
        .header{
            height: r(112);
            font-size: r(34);
            color:#fff;
            font-weight: bold;
            line-height: r(112);
            background:url("../../static/images/modal_header.png") no-repeat;
            background-size: r(580) r(112);
            text-align:center;
        }
        .content{
            margin-top:r(40);
            padding:0 r(54);
            line-height: r(49);
            font-size: r(28);
        }
        .footer{
            padding:r(10) r(40);
            overflow: hidden;
            div{
                width: r(230);
                height:r(92);
                border-radius: r(10);
                line-height: r(92);
                text-align: center;
                font-size: r(30);
                // margin-top: r(40);
            }
            .cancel{
                background: #e1e1e1;
                color: #333;
                float: left;
            }
            .sure{
                background: #0084ff;
                color: #fff;
                float: right;
            }
        }
    }
}
</style>


